.home__container {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-content: right;
  justify-content: center;
  margin-top: 60px;
}

.home__box1,
.home__box2,
.home__box3,
.home__box4 {
  background-color: white;
  width: 460px;
  height: 300px;
  margin: 20px 20px;
  /* box-shadow: 1px 1px black; */
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 13px 10px -7px rgba(0, 0, 0, 0.1);
}

.home__box1:hover,
.home__box2:hover,
.home__box3:hover,
.home__box4:hover {
  transform: scale(1.05, 1.05);
}
@media (max-width: 500px) {
  .home__box1,
  .home__box2,
  .home__box3,
  .home__box4 {
    width: 80%;
    height: 200px;
  }
}

.transaction__container {
  margin-top: 60px;
  width: 100%;
  height: 600px;
  display: flex;
  justify-content: center;
}
.transaction_list_container {
  width: 80%;
  height: 600px;
  /* background-color: red; */
  display: flex;
  flex-direction: column;
}
.transaction_list_item:hover {
  transform: translate3D(0, -1px, 0) scale(1.03);
}
.transaction_list_item {
  margin-top: 40px;
  border-radius: 10px;
  box-shadow: 1px 1px black;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background-color: white;
  padding: 10px 20px;
}
.transactin_list_item_row1 {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  position: relative;
  /* background-color: rebeccapurple; */
}
.transaction_description {
  display: flex;
  margin: 10px 60px;
}

.transaction_item_delete_btn {
  background-color: red;
  padding: 10px 20px;
  color: white;
  border-radius: 6px;
  border: none;
  font-weight: bold;
  letter-spacing: 0.6px;
  cursor: pointer;
}

.transaction_amount {
  color: green;
  font-size: 26px;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}

.transaction_item_description_heading {
  margin-top: 10px;
  text-decoration: underline;
  font-weight: bold;
  padding-left: 40px;
  font-size: 24px;
}

.transaction_item_description_btn:hover {
  cursor: pointer;
}
.transaction_item_description_btn {
  position: absolute;
  bottom: -30px;
  /* background-color: white; */
}

.transaction_item_description_unactive {
  display: none;
}
.transaction_item_description_active {
  display: flex;
  flex-wrap: wrap;
  margin-top: 20px;
  padding-top: 10px;
}

@media (max-width: 600px) {
  .transaction_list_container {
    width: 100%;
  }
}

@media (max-width: 450px) {
  .transaction_list_container {
    width: 80%;
  }
  .transaction_list_item {
    padding: 5px 10px;
  }
  .transaction_description {
    margin: 10px 20px;
  }
}

.navbar__logo {
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  color: black;
  text-transform: uppercase;
}

.navbar {
  width: 100vw;
  height: 60px;
  background-color: #ffac41;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: fixed;
  top: 0;
  z-index: 10;
}
.navbar__heading {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  color: black;
  padding-right: 20px;
  font-weight: 400;
}

.navbar__btn {
  margin-left: 10px;
}

.navbar__menu {
  position: absolute;
  top: 0px;
  left: -200px;
  display: flex;
  flex-direction: column;
  background-color: #ffac41;
  /* background-color: crimson; */
  width: 200px;
  height: 100vh;
  transition: 850ms;
}

.navbar__active {
  left: 0;
  z-index: 1;
}

.cancel__btn {
  display: flex;
  width: 100%;
  justify-content: flex-end;
  padding-right: 20px;
  padding-top: 20px;
  cursor: pointer;
  transition: 850ms;
}

.menu__item {
  text-decoration: none;
  color: black;
  padding: 6px 0px;
  text-align: center;
  font-weight: bold;
  border-radius: 30px;
  width: 160px;
  margin: 20px auto;
  display: flex;
  justify-content: flex-start;
  align-items: baseline;
  transition: 850ms;
}
.menu__item:hover {
  background-color: #393e46;
  transition: 850ms;
  color: white;
}
.cancel__btn :hover {
  background-color: red;
  transition: 850ms;
}

.menu__icon {
  margin: 0 20px;
}

.nav_profile_image {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-size: cover;
  margin-left: 30px;
  /* align-self: center; */
  border: none;
}

.nav_username {
  align-self: center;
  margin: 10px 0;
  color: #fff;
}

.nav_profile {
  background-color: #393e46;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.nav_menu_item_active {
  color: white;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #393e46;
  font-family: "Josefin Sans", sans-serif;
}

